@import '../scss/global/variables';
@import '../scss/global/mixins';
@import '../scss/global/placeholders';

// Placeholders and Mixins

%bootstrap-form-control {
	display: block;
	width: 100%;
	height: 50px;
	// padding: 6px 12px;
	// font-size: 15px;
	// line-height: 1.6;
	color: #4a4a4a;
	background-color: #fff;
	background-image: none;
	border: 2px solid #DDD;
	border-radius: 2px;
	// box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
	// transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;

	// lsx-form-control
	box-shadow: none;
	font-size: 18px;
	line-height: 1.33;
	padding: 10px 20px;
	transition: all 150ms ease;

	&:focus {
		border-color: #418AD0;
		// outline: 0;
		// box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(65, 138, 208, 0.6);
	}
}

%bootstrap-message {
	border-radius: 4px;
	border: 1px solid transparent;
	margin-bottom: 20px;
	padding: 15px !important;

	&:before { display: none; }
}

%bootstrap-message-success {
	background-color: #dff0d8;
	border-color: #d6e9c6;
	color: #3c763d;
}

%bootstrap-message-error {
	background-color: #f2dede;
	border-color: #ebccd1;
	color: #a94442;
}

%bootstrap-message-info {
	background-color: #d9edf7;
	border-color: #bce8f1;
	color: #31708f;
}

%small-form-field {
	height: 42px;
	padding: 10px 12px;
}

%small-form-select {
	height: 42px;
	padding: 0 12px;
}

%lsx-title {
	font-size: 30px !important;
	line-height: 1.2;
	margin-bottom: 4.25rem;
	margin-top: 0;
	text-align: center;

	&:after {
		content: '';
		display: block;
		height: 2px;
		margin: 2.4rem auto 0;
		width: 80px;
	}

	& > small {
		color: inherit;
		display: block;
		line-height: inherit;
		margin-top: 8px;

		&.long-text { margin-top: 16px; }
	}
}

@mixin small-button() {
	&,
	&:active,
	&:visited {
		font-weight: 400;
		height: initial;
		@include media('>tablet') {
		padding: 10px 30px !important;
		}
	}
}

/** Page Template Default **/
.page-template-page-php.tribe-js {
	&:not(.tribe-events-page-template) {
		&.post-type-archive-tribe_events {
			.wrap.container {
				#primary {
					.entry-content {
						width: 100%;
						padding: 0;
						> .archive-header-wrapper {
							display: none;
						}
						#tribe-events {
							.type-tribe_events {
								.entry-summary {
									padding: 0;
								}
							}
						}
					}
				}
			}
		}
		&.single {
			.wrap.container {
				.content.row {
					> .archive-header-wrapper {
						display: none;
					}
					> #primary {
						margin-top: 0;
						z-index: 0;
					}
				}
			}
		}
	}
}

