/**
 * Tribe Community Event List
 */

#tribe-community-events, #tribe-events {
	form {
		.tribe-events-community-details {
			background-color: #fff;
			//padding: 1em 2em;
		}
		div.venue, .events-community-post-title, .events-community-post-content {
			display: flex;
			flex-direction: column;
			label {
				font-size: 16px;
				font-weight: 400;
				text-transform: uppercase;
			}
			input:not([type='checkbox']):not(.button), textarea {
				display: block;
				width: 100%;
				height: $input-height-base;
				padding: $padding-base-vertical $padding-base-horizontal;
				font-size: $font-size-base;
				line-height: $line-height-base;
				color: $input-color;
				background-color: $input-bg;
				background-image: none;
				border: 2px solid $input-border;
				border-radius: $input-border-radius;
				@include input-size('.input-lg', $input-height-large, $padding-large-vertical, $padding-large-horizontal, $font-size-large, $line-height-large, $input-border-radius-large);
				@include box-shadow(inset 0 1px 1px rgba(0,0,0,.075));
  				@include transition(border-color ease-in-out .15s, box-shadow ease-in-out .15s);
				@include form-control-focus;
				@include placeholder;
				&::-ms-expand {
					border: 0;
					background-color: transparent;
				  }
				&[disabled],
				&[readonly],
				fieldset[disabled] & {
					background-color: $input-bg-disabled;
					opacity: 1;
				}
				&[disabled],
				fieldset[disabled] & {
				cursor: $cursor-disabled;
				}
			}
			input[type='checkbox'] {
				margin: 5px 0 20px;
			}
			textarea {
				height: auto;
			}
		}
		.tribe-section-content, .tribe-section-content-field {
			ul {
				border: none;
				box-shadow: none;
			}
			li, input:not(.select2-input):not([type='checkbox']):not(.tribe-datepicker):not(.tribe-timepicker) {
				border: 2px solid $input-border;
				border-radius: $input-border-radius;
				@include box-shadow(inset 0 1px 1px rgba(0,0,0,.075));
  				@include transition(border-color ease-in-out .15s, box-shadow ease-in-out .15s);
				@include form-control-focus;
				@include placeholder;
				height: 50px;
				padding: 5px 15px;
				line-height: 35px;
				width: 100%;
			}
		}
		.select2-container.tribe-dropdown:not(.select2-container-multi) {
			width: 100%;
		}
		.select2-choice {
			border: 2px solid $input-border;
			border-radius: $input-border-radius;
			@include box-shadow(inset 0 1px 1px rgba(0,0,0,.075));
			@include transition(border-color ease-in-out .15s, box-shadow ease-in-out .15s);
			@include form-control-focus;
			@include placeholder;
			height: 50px;
			padding: 5px 10px;
			line-height: 35px;
		}
	}
}


