/* Tribe Community Events buttons aligned left */
a.tribe-add-post.tribe-button, button#tribe-add-recurrence {
    margin-left: 0;
    margin-right: 0;
}

/**
 * Tribe Community Events Add new
 */
.tribe-community-events.form, #tribe-community-events.form {
	padding: 0;
	background: transparent;
	border: none;

	.tribe-button {
		width: auto;
	}

	.tribe-field-start_date, .tribe-field-end_date, .tribe-field-start_time, .tribe-field-end_time {
		height: auto;
		color: #4A4A4A;
		background-color: #fff;
		background-image: none;
		border: 2px solid #DDD;
		border-radius: 2px;
		padding: 4px 10px;
		width: auto;

		&:focus {
			border-color: #418AD0;
			outline: 0;
		}
	}

	.my-events-header {
		background-color: #fff;
		padding: 16px;
		border-left: 1px solid #e0e0e0;
		border-right: 1px solid #e0e0e0;
		border-top: 1px solid #e0e0e0;
		border-bottom: 1px solid #e0e0e0;
		margin: 0;
		margin-bottom: 1.5em;
		h2.my-events {
			flex-grow: 10;
		}

		.tribe-button {
			flex-grow: 1;
		}
	}

	.tribe-section, .events-community-post-content, .events-community-post-title {
		background: #fff;
		border-radius: 3px;
		font-size: 13px;
	}

	.tribe-section {
		margin: 24px 0;
		padding-bottom: 32px;
		border: 1px solid #e0e0e0;
	}

	.events-community-post-title, .events-community-post-content {
		padding: 16px;
		font-weight: normal;
		border-top: 0;
		border-radius: 0;
		border: 1px solid #e0e0e0;

		label {
			display: block;
			font-weight: normal;
			margin: 0;
		}
	}

	.events-community-post-title {
		border-bottom: 0px;
		border: 1px solid #e0e0e0;
		display: block;
		margin: 0;
		//padding-bottom: 0px;

		#post-title {
			margin-bottom: 0;
			width: 100%;
			display: block;
		}
	}

	.events-community-post-content {
		border-bottom: 1px solid #e0e0e0;
		//padding-top: 0px;
		padding-bottom: 25px;
	}
}
.tribe-community-event-info {
	width: inherit;
}
.tribe-community-event-info {
	.organizer {
		display: flex;
		flex-direction: column;
		//width: 50%;

		input {
			display: block;
			height: $input-height-base;
			padding: $padding-base-vertical $padding-base-horizontal;
			font-size: $font-size-base;
			line-height: $line-height-base;
			color: $input-color;
			background-color: $input-bg;
			background-image: none;
			border: 2px solid $input-border;
			border-radius: $input-border-radius;
			@include input-size('.input-lg', $input-height-large, $padding-large-vertical, $padding-large-horizontal, $font-size-large, $line-height-large, $input-border-radius-large);
			@include box-shadow(inset 0 1px 1px rgba(0,0,0,.075));
			@include transition(border-color ease-in-out .15s, box-shadow ease-in-out .15s);
			@include form-control-focus;
			@include placeholder;
			&::-ms-expand {
				border: 0;
				background-color: transparent;
				}
			&[disabled],
			&[readonly],
			fieldset[disabled] & {
				background-color: $input-bg-disabled;
				opacity: 1;
			}
			&[disabled],
			fieldset[disabled] & {
			cursor: $cursor-disabled;
			}
		}
	}
}

